import * as THREE from "three";
import * as THREEx from "./ar-threex-location-only"



// Scene
const scene = new THREE.Scene();

/**
 * Base + Box
 */
const cube = new THREE.BoxGeometry(1, 1, 1);
const cubeMat = new THREE.MeshStandardMaterial({ color: 0xff0000 });
cubeMat.roughness = 0.4;
const cubeMesh = new THREE.Mesh(cube, cubeMat);
cubeMesh.castShadow = true;
scene.add(cubeMesh);

/**
 * Sizes
 */

const sizes = {
  width: window.innerWidth,
  height: window.innerHeight
};

/**
 * lights
 */

const light1 = new THREE.PointLight({ color: "white" }, 1);
light1.position.set(0, 3, 2);
light1.castShadow = true;
light1.shadow.mapSize.width = 1024;
light1.shadow.mapSize.height = 1024;
light1.shadow.radius = 5;
scene.add(light1);

/**
 * Camera
 */
const camera = new THREE.PerspectiveCamera(80, sizes.width / sizes.height);
camera.position.z = 3;
scene.add(camera);

/**
 * Renderer
 */
const canvas = document.querySelector("#webgl");
const renderer = new THREE.WebGLRenderer({
  canvas: canvas
});
renderer.setSize(sizes.width, sizes.height);
renderer.setPixelRatio( window.devicePixelRatio );
renderer.shadowMap.enabled = true;

const cam = new THREEx.WebcamRenderer(renderer, "#video1");

/**
 * Animate
 */

const resizeUpdate = () =>{
  const canvas = renderer.domElement;
  const width = canvas.clientWidth,
    height = canvas.clientHeight;
  if (width != canvas.width || height != canvas.height) {
    renderer.setSize(width, height, false);
  }
  camera.aspect = canvas.clientWidth / canvas.clientHeight;
  camera.updateProjectionMatrix();
}

const tick = () => {
  resizeUpdate();

  cubeMesh.rotateX(0.01);
  cubeMesh.rotateY(0.03);
  // Render
  cam.update();
  renderer.render(scene, camera);

  // Call tick again on the next frame
  window.requestAnimationFrame(tick);
};

tick();
